.header {
	font-family: 'TT-travels';
	font-weight: 600;
    font-style: normal;
	font-size: 24px;
	color: white;
	padding: 8vh;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.block {
	min-height: 100vh;
}
.block-header {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	padding: 6vh;
	color: white;
}
.button-cr {
	font-weight: 500;
	font-size: 18px;
	background-color: white;
	color: #17191F;
	padding: 8px 18px;
	border-radius: 24px;
	cursor: pointer;
}
.menu {
	font-family: 'TT-travels';
	font-weight: 400;
    font-style: normal;
	color: white;
	display: flex;
	width: 100%;
	height: 25px;
	justify-content: space-around;
	align-items: flex-end;
}
.menu > div {
	cursor: pointer;
	border: #ffffff solid 0.5px;
	background-color: #ffffff1e;
	backdrop-filter: blur(24px);
	padding: 12px 18px;
	border-radius: 24px;
}

#main-block {
	background-image: url('./images/bg-main.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}
#create-request {
	background-image: url('./images/bg-create-request.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

#main-block > h1 {
	font-family: "TT-travels";
	font-weight: 500;
    font-style: italic;
	color: white;
	text-align: center;
	font-size: 38px;
	margin: 15vh;
}

#about-block > h2 {
	font-family: 'TT-travels';
	font-weight: 500;
    font-style: normal;
	margin-top: 10vh;
	margin-left: 10vh;
	color: white;
	width: 100vh;
	font-size: 21px;
}
.numbers {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 10vh;
}
.number-item {
	display: flex;
	padding-bottom: 10vh;
}
.number-item > h1 {
	font-family: 'TT-travels';
	font-weight: 500;
    font-style: normal;
	font-size: 86px;
	margin: 0px;
	color: white;
}
.number-item > div {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	font-size: 14px;
	margin: 0px 20px;
	color: white;
	display: flex;
	align-items: center;
	width: 250px;
}

.services-list {
	list-style-type: none;
	padding-right: 50px;
	border-right: thick solid #ffffff;
	border-width: 1px;
	display: inline-block;
}
.list-item {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	font-size: 18px;
	color: white;
	padding: 10px;
	background-color: transparent;
	cursor: pointer;
}

.desc {
	display: flex;
	max-width: 750px;
}
.desc > div {
	flex: 2;
	padding: 18px;
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	color: white;
	font-size: 18px;
}
.desc > #exampels {
	flex: 1,
}

.desc > div > h1 {
	padding-bottom: 12px;
	font-size: 18px;
	font-weight: 300;
}
.desc > div > a {
	display: block;
	padding: 12px 0px;
	color: white;
}
.desc > div > div {
	padding-top: 72px;
	color: white;
	font-style: 'TT-travels';
	font-weight: 600;
    font-style: normal;
}


.form {
	display: block;
	margin: 12px;
	margin-left: 15vh;
	border: none;
	border-bottom: 1px solid white;
	background-color: transparent;
	outline: none;
	color: white;
	padding: 8px;
	font-size: 18px;
	width: 300px;
}
#create-request > div {
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: start;
	justify-content: center;
}
#create-request > div > input::-webkit-input-placeholder {
	color: white;
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	text-align: center;
}
#create-request > div > select {
	display: block;
	margin: 12px;
	margin-left: 15vh;
	border: none;
	border-bottom: 1px solid white;
	background-color: transparent;
	outline: none;
	color: white;
	padding: 8px;
	font-size: 18px;
	width: 316px;
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	text-align: center;
}
#create-request > div > select > option {
	background-color: #17191F;
}
#create-request > div > .button {
	width: 316px;
	display: block;
	margin: 12px;
	margin-left: 15vh;
	border: none;
	background-color: white;
	outline: none;
	color: #17191F;
	padding: 8px;
	font-size: 18px;
	border-radius: 12px;
}

.footer {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	padding: 48px 72px;
	font-size: 18px;
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	color: #ffffffbb;
}


.project-card {
	min-width: 400px;
	margin: 0px 18px;
	height: 400px;
	border-radius: 18px;
	background-image: url('./images/example-1.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}
.project-card > div {
	display: flex;
	align-items: end;
	height: 100%;
	border-radius: 18px;
	background: linear-gradient(#ffffff00 60%, #000000a4 , #000000);
}
.project-card > div > div {
	width: 100%;
	padding: 20px 14px;
}
.project-card > div > div > h1 {
	margin: 8px 0px;
	font-family: 'TT-travels';
    font-style: normal;
	font-weight: 600;
	color: white;
}
.project-card > div > div > div {
	font-family: 'TT-travels';
    font-style: normal;
	font-weight: 300;
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
#bmore {
	background-color: white;
	padding: 8px;
	border-radius: 16px;
	color: #17191F;
	font-weight: 500;
	font-size: 14px;
}
#listport {
	display: flex;
	flex-direction: row;
	overflow: scroll hidden;
}
#listport::-webkit-scrollbar {
	width: 0;
}

#about-image, #about-image1 {
	background-image: url('./images/about-us.jpg');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	border-top-left-radius: 90px;
	border-bottom-left-radius: 90px;
	height: 60vh;
	min-width: 60vh;
}
#about-image1 {
	background-image: url('./images/about-us1.jpg');
}
#about-us-page > #block-about-us {
	display: flex;
	margin: 0px 100px;
	background-color: #1d1f25;
	border-radius: 90px;
	color: white;
	margin-bottom: 50px;
}
#about-us-page > div > #text-about {
	padding: 48px;
	font-family: 'TT-travels';
    font-style: normal;
	font-weight: 300;
	font-size: 20px;
}
#icon {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;	
	color: white;
}